.page__menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}

.top-bar {
  padding: 24px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (min-width: 744px) {
    padding: 36px 72px;
  }

  @media (min-width: 1260px) {
    padding: 36px 120px;
  }
}

.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: $menu-color;
  font-weight: 700;
  text-transform: uppercase;
  overflow: auto;

  &__logo {
    width: 130px;

    @media (min-width: 1260) {
      width: 187px;
    }
  }
}

.menu__top {
  margin-bottom: 24px;
}

.menu__nav {
  margin-bottom: 40px;
}

.menu__content {
  padding-inline: 20px;

  @media (min-width: 744) {
    padding-inline: 72px;
  }

  @media (min-width: 1260) {
    padding-inline: 120px;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.nav__link {
  text-decoration: none;
  color: $text-secondary-color;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
  font-weight: 600;
}

.menu__phone-number {
  display: block;
  text-decoration: none;
  color: $text-secondary-color;
  font-size: 16px;
  line-height: 27px;
  font-weight: 600;
  margin-bottom: 16px;
}

.menu__test-ride {
  display: block;
  text-decoration: none;
  color: $text-secondary-color;
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
  letter-spacing: 2px;
  padding-bottom: 7px;
  border-bottom: 1px solid $text-secondary-color;
  width: fit-content;
}
