.contact {
  padding-top: 40px;
}

.contact__block {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact__block--title {
  color: $text-accent-color;
  font-size: 32px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -1.5px;
  text-align: center;
  margin: 0 0 48px;

  @media (min-width: 744px) {
    font-size: 48px;
    line-height: 48px;
  }

  @media (min-width: 1260px) {
    font-size: 64px;
    line-height: 64px;
  }
}

.contact__block--card {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @media (min-width: 744) {
    flex-direction: row;
    justify-content: center;
    gap: 24px;
  }

  @media (min-width: 1260px) {
    gap: 198px;
    justify-content: flex-start;
  }
}

.contact__block--form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 280px;
  width: 100%;
  margin-bottom: 48px;

  @media (min-width: 1260px) {
    max-width: 411px;
  }
}

.contact__block--form input {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.form-name,
.form-email {
  height: 48px;
  border-radius: 40px;
  margin-bottom: 16px;
  padding-left: 16px;
  color: rgba(126, 126, 131, 1);
}

.form-textArea {
  height: 148px;
  border-radius: 16px;
  resize: none;
  padding-top: 14px;
  padding-left: 16px;
  margin-bottom: 32px;
  color: rgba(126, 126, 131, 1);
}

.form-button {
  background-color: $secondary-color;
  color: $text-secondary-color;
  height: 56px;
  border-radius: 40px;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
}

.form-button:hover {
  box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.4);
}

.form-button:active {
  box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.64);
}

.contact__block--contacts {
  display: flex;
  flex-direction: column;
  max-width: 100%;
}

.contacts--phone,
.contacts--email,
.contacts--address {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 24px;
}

.contacts--phone span,
.contacts--email span,
.contacts--address span {
  font-size: 14px;
  font-weight: 400;
  line-height: 19.6px;
  color: $text-accent-color;
}

.contacts--phone a,
.contacts--email a,
.contacts--address a {
  font-size: 16px;
  font-weight: 500;
  line-height: 22.4px;
  text-align: left;
  text-decoration: none;
  color: $text-accent-color;
}
