.about {
  padding-bottom: 60px;
}

.about__block {
  display: flex;
  flex-direction: column;

  &--title {
    font-size: 32px;
    font-weight: 500;
    line-height: 32px;
    letter-spacing: -1.5px;
    color: $text-accent-color;
    margin: 0 0 32px;
  }

  &--text {
    font-size: 23px;
    font-weight: 500;
    line-height: 32.2px;
    color: $text-accent-color;
    margin: 0;
  }
}

@media (min-width: 744px) {
  .about__block {
    flex-direction: row;
    gap: 24px;
    justify-content: space-between;

    &--title {
      font-size: 48px;
      line-height: 48px;
    }

    &--text {
      max-width: 288px;
    }
  }
}

@media (min-width: 1260px) {
  .about__block {
    align-content: center;

    &--title {
      font-size: 64px;
      line-height: 64px;
    }

    &--text {
      max-width: 498px;
    }
  }
}
