$main-color: rgba(29, 29, 29, 1);
$secondary-color: rgba(248, 248, 250, 1);
$menu-color: rgba(194, 194, 194, 1);
$text-accent-color: rgba(255, 255, 255, 1);
$text-secondary-color: rgba(29, 29, 29, 1);

.page {
  scroll-behavior: smooth;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page_body {
  margin: 0;
  background-color: $main-color;
}

.container {
  padding: 60px 20px;
  width: 280px;
  justify-content: center;
  margin: 0 auto;

  @media (min-width: 744) {
    width: 600px;
  }

  @media (min-width: 1260) {
    width: 1020px;
  }
}

@import "./blocks/header";
@import "./blocks/menu";
@import "./blocks/about";
@import "./blocks/compare";
@import "./blocks/details";
@import "./blocks/contact";
@import "./blocks/footer";
