.details {
  padding-top: 32px;
  padding-bottom: 40px;
}

.details__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;

  &--title {
    color: $text-accent-color;
    font-size: 32px;
    font-weight: 500;
    line-height: 32px;
    letter-spacing: -1.5px;
    text-align: center;
    margin: 0 0 48px;
  }
}

.details__block--cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.details__block--card {
  display: flex;
  flex-direction: column;
  margin: 0 0 40px;
}

.details__block--card-pictures {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  justify-content: center;
  margin: 0 0 16px;
  width: 100%;
}

.details__block--card-pictures img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.details__block--card-detail {
  display: flex;
  flex-direction: column;
  max-width: 100%;
}

.details__block--card-title {
  color: $text-accent-color;
  font-size: 32px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -1.5px;
  margin: 0 0 8px;
  width: 100%;
}

.details__block--card-text {
  color: $text-accent-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 22.4px;
  max-width: 672px;
}

.details__block--button {
  display: inline-block;
  background-color: $secondary-color;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  text-align: center;
  justify-items: center;
  color: $text-secondary-color;
  padding-top: 15px;
  padding-bottom: 15px;
  width: 100%;
  max-width: 280px;
  border-radius: 40px;
}

.details__block--button:hover {
  box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.4);
}

.details__block--button:active {
  box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.64);
}

@media (min-width: 744px) {
  .grid-picture-1,
  .grid-picture-2 {
    grid-template-columns: 392px 184px;
  }

  .grid-picture-3 {
    grid-template-columns: 184px 395px;
  }

  .details__block--title {
    font-size: 48px;
    line-height: 48px;
  }
}

@media (min-width: 1260px) {
  .grid-picture-1,
  .grid-picture-2 {
    grid-template-columns: 672px 324px;
  }

  .grid-picture-3 {
    grid-template-columns: 324px 672px;
  }

  .details__block--title {
    font-size: 64px;
    line-height: 64px;
  }
}
